import tools from '@/common/tools.js'
export default {
  name: 'search',
  data: function() {
    return {
      searchNum: 99999,
      historyData: [],
      showHistoryNum: 6
    }
  },
  created() {
    let historyData = tools.getStorage('searchData')
    if (historyData) {
     this.historyData = historyData
    }
  },
  methods: {
    startSearch() {
      let searchText = this.$refs.searchText.value
      if (tools.trim(searchText) !== '') {
        this.changeSaveHistory(searchText)
        this.historyGo(searchText)
        // console.log(searchText)
      }
    },
    historyGo(text) {
      this.$router.push({path: 'searchresult', query: { keyword: text, category: 0 }})
    },
    changeSaveHistory(text) {
      let data = []
      if (!tools.getStorage('searchData')) {
        data.push(text)
      } else {
        data = tools.getStorage('searchData')
        for (let i = 0; i < data.length; i++) {
          if (text === data[i]) {
            data.splice(i, 1)
          }
        }
        this.searchNum--
        data.unshift(text)
        console.log('合并后的数据', data)
        if (data.length > this.showHistoryNum) {
          data.pop()
        }
      }
      this.historyData = data
      tools.setStorage('searchData', data)
    },
    goBack() {
      console.log('我要路由返回了')
      this.$router.go(-1)
    }
  }
}



